<template>
  <div class="bg">
    <Nav :menusData="menusdata" :matchData="true"></Nav>
    <div class="block4">
      <div class="block4_content">
        <div class="block4_content_title">
          <img src="../../assets/img/vip/icon-1.png" alt />
          请选择{{vipData.data.name}}套餐
        </div>
        <div class="block4_content_box">
          <div class="block4_content_box_list">
            <ul>
              <li
                :class="{'active':index == vipData.active }"
                v-for="(item, index) in vipData.data.memberSystems"
                :key="index"
                @click="vipData.active = index"
              >
                <span>
                  <a>{{(vipData.data.price * item.discount * item.duration).toFixed()}}</a>
                  元/{{item.duration}}年
                </span>
                <span>
                  <img src="../../assets/img/vip/icon-1.png" alt />
                  {{item.content}}
                  <br />
                  赠送{{item.deliveryNumber}}条投递条数
                </span>
                <span v-if="index == vipData.data.memberSystems.length - 1" class="hot">推 荐</span>
              </li>
            </ul>
            <div v-if="vipData.active != -1">
              <p class="block4_content_box_list_pay_title">
                使用微信扫描二维码付款:
                <span>{{(vipData.data.price * vipData.data.memberSystems[vipData.active].discount * vipData.data.memberSystems[vipData.active].duration).toFixed()}}</span>
                元/{{vipData.data.memberSystems[vipData.active].duration}}年
              </p>
              <p style="text-align: center;">
                <img src="../../assets/img/vip/qrcode.jpg" alt />
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Nav from "../../components/base/nav/menus";
import { getMemberSearchByCode } from "../../api/public";
export default {
  components: {
    Nav
  },
  data() {
    return {
      menusdata: [
        {
          name: "总平台首页",
          url: "/index"
        },
        {
          name: "项目首页",
          url: "/project/index"
        },
        {
          name: "资金首页",
          url: "/capital/index"
        }
      ],
      vipData: {
        data: {},
        active: -1 //0月 1季度 2年
      }
    };
  },
  created() {
    getMemberSearchByCode({ code: this.$route.params.code }).then(res => {
      this.vipData.data = res.data.data;
    });
  }
};
</script>
<style lang="less" scoped>
.bg {
  background: #ffffff;
}
.block4 {
  background: #f5f5f5;
  padding: 20px 0;
  &_content {
    background: #ffffff;
    width: 1024px;
    margin: 0 auto;
    &_title {
      line-height: 80px;
      font-size: 22px;
      text-align: center;
      border-bottom: 1px solid #dedede;
      img {
        vertical-align: middle;
        margin-right: 10px;
      }
    }
    &_box {
      &_title {
        justify-content: center;
        padding: 40px 0 0 0;
        font-size: 18px;
      }
      &_content {
        padding: 40px;
        line-height: 20px;
        font-size: 14px;
      }
      &_list {
        padding-bottom: 80px;
        min-height: 463px;
        ul {
          margin: 20px 0;
          display: flex;
          justify-content: center;
          flex-wrap: wrap;
          li {
            width: 300px;
            cursor: pointer;
            margin: 20px;
            padding: 10px 30px;
            text-align: center;
            display: flex;
            flex-flow: column;
            border: 1px solid #e6e6e6;
            font-size: 14px;
            border-radius: 12px;
            position: relative;
            span {
              padding: 15px 0;
              line-height: 30px;
              a {
                font-size: 48px;
              }
              img {
                margin-right: 8px;
                vertical-align: middle;
              }
            }
            span:nth-child(1) {
              border-bottom: 1px solid #e6e6e6;
              padding-top: 25px;
            }
            .hot {
              position: absolute;
              background: #ff470a;
              left: 0px;
              top: 0px;
              font-size: 14px;
              color: #ffffff;
              padding: 2px 20px;
              border-radius: 12px 0px 12px 0px;
            }
          }
          li:hover {
            background: url(../../assets/img/vip/list-hover.png);
            background-size: 100% 100%;
            border: 1px solid #ff470a;
          }
          .active {
            background: url(../../assets/img/vip/list-hover.png);
            background-size: 100% 100%;
            border: 1px solid #ff470a;
          }
        }
        &_pay_title {
          text-align: center;
          padding: 40px 0px 30px 0px;
          font-size: 18px;
          span {
            color: #ff470a;
            font-size: 33px;
            margin: 0 10px;
          }
        }
      }
    }
  }
}
</style>